@use 'base/mixins' as m;
@use 'base/variables' as v;

@include m.tablet {
    .contenedor-anuncios {
        @include m.grid(3,2rem);
       
    }
}

.anuncio {
    background-color: lighten($color: v.$gris, $amount: 5);
    border: 1px solid darken($color: v.$gris, $amount:10);
    margin-bottom: 2rem;
    .contenido-anuncio {
        padding: 2rem;
    }

    h3,p {
        margin: 0;
    }
}

.precio {
    color: v.$verde;
    font-weight: v.$bold;
    font-size: 3rem;
}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    
    li {
        flex: 1;
        display: flex;

        img {
            flex: 0 0 .5rem;
            margin-right: 1rem;
        }
        p {
            font-weight: v.$black;
        }
    }
}